<template>
	<div class="all-img">
		<el-col :span="5" v-for="item in detailList" :key="item.id" :offset="1">
			<div class="thumbnail"  @click="changeDialog(item.id)">
				<el-image :src="item.url" fit="cover">
					<div slot="placeholder" class="image-slot">
						加载中<span class="dot">...</span>
					</div>
				</el-image>
			</div>
		</el-col>
		<detail-info
				:dialog-table-visible.sync="dialogTableVisible"
				v-if="dialogTableVisible"
				:detail-data.sync="detailInfo"
				:detail-list.sync="detailList"
				:collection="3"
		></detail-info>
	</div>
</template>

<script>
	import { getWorks } from '@/service/user-api'
	import DetailInfo from "@/views/detail/detail-info"
	export default {
		name: "Works",
		components: {
			DetailInfo
		},
		created() {
			getWorks(this.userId)
			.then((res) => {
				if (res.code === 2000){
					this.detailList = res.work_list
				}
			})

		},
		data() {
			return {
				dialogTableVisible: false,
				detailList: [],
				userId: this.$store.getters.consumerInfo.id,
				detailInfo: {}
			}
		},
		methods: {
			changeDialog(id){
				this.dialogTableVisible = true
				this.detailInfo = this.detailList.find(item => item.id === id)
			},
		}
	}
</script>

<style lang="scss">
	.all-img{
		width: 80%;
		margin-left: 130px;
		.el-col{
			height: 237px;
			line-height: 237px;
		};
		.thumbnail{
			height: 217px;
			width: 250px;
			background-color: black;
			padding: 0 10px;
			max-width: 290px;
			border-radius: 10px;
			.el-image{
				height: 217px;
				width: 250px;
			}
		}
	}

</style>
